

.about-container {
  font-family: Arial, sans-serif;
  background-color: #faf3e0;
  margin-top: 60px;
  padding: 20px;
  max-width: 500px;
  margin: 0 auto;
  border-radius: 10px;
  border: 1px solid #d3d3d3;
  margin-top: 5%;
}

.about-header {
  text-align: center;
  margin-bottom: 20px;
}

.profile-pic {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 10px;
}

h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.contact-us-btn {
  background-color: #efefef;
  border: 1px solid #d3d3d3;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.about-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  position: relative;
}

.form-group label {
  flex: 1;
  margin-right: 10px;
}

.form-group input {
  flex: 2;
  padding: 8px;
  border: 1px solid #d3d3d3;
  border-radius: 5px;
}

.edit-icon {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.half-width {
  flex: 1;
  margin-right: 10px;
}

